.header {
    position: sticky;
    top: 0;
    z-index: 100;
    background: rgba(15, 23, 42, 0.8);
    backdrop-filter: blur(20px);
    border-bottom: 1px solid rgba(148, 163, 184, 0.1);
}

.header-container {
    max-width: 1400px;
    margin: 0 auto;
    padding: 1.5rem 2rem;
}

.header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-title {
    font-size: 2.5rem;
    font-weight: 800;
    color: #f9fafb;
    margin: 0;
    background: linear-gradient(135deg, #f9fafb, #cbd5e1);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.profile-btn {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
}

.profile-btn:hover {
    background: rgba(255, 255, 255, 0.2);
    transform: scale(1.05);
}

.profile-icon {
    font-size: 1.5rem;
}

@media (max-width: 768px) {
    .header-container {
        padding: 1rem 1.5rem;
    }
    
    .header-title {
        font-size: 2rem;
    }
    
    .profile-btn {
        width: 40px;
        height: 40px;
    }
    
    .profile-icon {
        font-size: 1.2rem;
    }
}
